import request from '@/utils/request'


export function getInfo () {
  return request({
    url: '/admin/info',
    method: 'get',
  })
}

export function logout () {
  return request({
    url: '/admin/logout',
    method: 'post'
  })
}

export function fetchList (params) {
  return request({
    url: '/admin/list',
    method: 'get',
    params: params
  })
}

export function createAdmin (data) {
  return request({
    url: '/admin/register',
    method: 'post',
    data: data
  })
}

export function updateAdmin (id, data) {
  return request({
    url: '/admin/update/' + id,
    method: 'post',
    data: data
  })
}

export function updateStatus (id, params) {
  return request({
    url: '/admin/updateStatus/' + id,
    method: 'post',
    params: params
  })
}

export function deleteAdmin (id) {
  return request({
    url: '/admin/delete/' + id,
    method: 'post'
  })
}

export function getRoleByAdmin (id) {
  return request({
    url: '/admin/role/' + id,
    method: 'get'
  })
}

export function allocRole (data) {
  return request({
    url: '/admin/role/update',
    method: 'post',
    data: data
  })
}

// 
export function login (account, password) {
  return request({
    url: `/sso/login?account=${account}&password=${password}`,
    method: 'post',
  })
}

// 
export function register (data) {
  return request({
    url: `/sso/register?account=${data.account}&authCode=${data.authCode}&nickName=${data.nickName}&password=${data.password}`,
    method: 'post',
  })
}

// 
export function registerAuthCodeSend (data) {
  return request({
    url: `/sso/register/authCode/send?account=${data}`,
    method: 'post',
  })
}

// 
export function registerCheckAuthCodeSend (data, code) {
  return request({
    url: `/sso/register/checkAuthCode?account=${data}&authCode=${code}`,
    method: 'post',
  })
}

// 
export function forgetPassword (data) {
  return request({
    url: `/sso/forgetPassword?account=${data.account}&authCode=${data.authCode}&password=${data.password}`,
    method: 'post',
  })
}

// 
export function forgetPasswordAuthCodeSend (data) {
  return request({
    url: `/sso/forgetPassword/authCode/send?account=${data}`,
    method: 'post',
  })
}

// 
export function userCheck (params) {
  return request({
    url: '/sso/user/check',
    method: 'get',
    params: params
  })
}

// token
export function refreshToken () {
  return request({
    url: '/sso/refreshToken',
    method: 'get',
  })
}

