<template>
    <div class="m-video" :class="{'u-video-hover': !hidden}" :style="`width: ${width}px; height: ${height}px;`">
      <video
        ref="veo"
        :style="`object-fit: ${zoom};`"
        :src="src"
        :poster="veoPoster"
        :width="width"
        :height="height"
        :autoplay="autoplay"
        :controls="!originPlay&&controls"
        :loop="loop"
        :muted="autoplay || muted"
        :preload="preload"
        crossorigin="anonymous"
        @loadeddata="poster ? () => false : getPoster()"
        @pause="showPlay ? onPause() : () => false"
        @playing="showPlay ? onPlaying() : () => false"
        @click.prevent.once="onPlay"
        v-bind="$attrs">
        video
      </video>
      <svg v-show="originPlay || showPlay" class="u-play" :class="{'hidden': hidden}" :style="`width: ${playWidth}px; height: ${playWidth}px;`" viewBox="0 0 24 24">
        <!-- <path stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5" d="M4.75 6.75C4.75 5.64543 5.64543 4.75 6.75 4.75H17.25C18.3546 4.75 19.25 5.64543 19.25 6.75V17.25C19.25 18.3546 18.3546 19.25 17.25 19.25H6.75C5.64543 19.25 4.75 18.3546 4.75 17.25V6.75Z"></path> -->
        <path stroke-linecap="round" stroke-linejoin="round" stroke-width="1.5" d="M15.25 12L9.75 8.75V15.25L15.25 12Z"></path>
      </svg>
    </div>
  </template>
  <script>
  export default {
    name: 'Video',
    props: {
      src: { // url https  require('@/assets/files/Bao.mp4')
        type: String,
        required: true,
        default: ''
      },
      poster: { // url https  require('@/assets/images/Bao.jpg')
        type: String,
        default: ''
      },
      second: { //  second 
        type: Number,
        default: 0.5
      },
      width: { // 
        type: Number,
        default: 800
      },
      height: { // 
        type: Number,
        default: 450
      },
      /*
         MDN https://developer.mozilla.org/zh-CN/docs/Web/Media/Autoplay_guide
        Autoplay 
        
        1. 0
        2.
        3.
        4.<iframe>
        autoplayChromeChromium
        videoaudioautoplay
         autoplay  muted: true 
        
      */
      autoplay: { // 
        type: Boolean,
        default: false
      },
      controls: { // 
        type: Boolean,
        default: true
      },
      loop: { // 
        type: Boolean,
        default: false
      },
      muted: { // 
        type: Boolean,
        default: false
      },
      preload: { // autoplaypreload
        type: String,
        default: 'metadata' // auto:; metadata: none:
      },
      showPlay: { // 
        type: Boolean,
        default: true
      },
      playWidth: { // 
        type: Number,
        default: 96
      },
      zoom: { // videoposter
        type: String,
        default: 'contain' // none:(); fill:; contain:; cover:
      }
    },
    data () {
      return {
        veoPoster: this.poster,
        originPlay: true,
        hidden: false
      }
    },
    mounted () {
      if (this.autoplay) {
        this.hidden = true
        this.originPlay = false
      }
      /*
        
        vue2this.$refs.veo.playbackRate = 2
        vue3veo.value.defaultPlaybackRate = 2
      */
      // this.$refs.veo.playbackRate = 2
    },
    methods: {
      /*
        loadeddata 
        preloadnone
      */
      getPoster () { // 0.5s
        // 0.5s
        this.$refs.veo.currentTime = this.second
        // canvas
        const canvas = document.createElement('canvas')
        const ctx = canvas.getContext('2d')
        // canvas
        canvas.width = this.$refs.veo.videoWidth
        canvas.height = this.$refs.veo.videoHeight
        ctx.drawImage(this.$refs.veo, 0, 0, canvas.width, canvas.height)
        // canvasbase64
        this.veoPoster = canvas.toDataURL('image/png')
      },
      onPlay () {
        if (this.originPlay) {
          this.$refs.veo.currentTime = 0
          this.originPlay = false
        }
        if (this.autoplay) {
          this.$refs.veo.pause()
        } else {
          this.hidden = true
          this.$refs.veo.play()
        }
      },
      onPause () {
        this.hidden = false
      },
      onPlaying () {
        this.hidden = true
      }
    }
  }
  </script>
  <style lang="scss" scoped>
  * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  .m-video {
    display: inline-block;
    position: relative;
    background: #000;
    cursor: pointer;
    .u-play {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      fill: none;
      color: #FD527D;
      pointer-events: none;
      opacity: 0.7;
      transition: opacity .3s;
      path {
        stroke: #FFF;
      }
    }
    .hidden {
      opacity: 0;
    }
  }
  .u-video-hover {
    &:hover {
      .u-play {
        opacity: 0.9;
      }
    }
  }
  </style>
  
  